import * as S from "./styles.ts";

const Home = () => {
  return (
    <S.Content>
      <S.Title>O que so Atividades Complementares (ACs):</S.Title>
      <S.Text>
        As ACs so atividades obrigatrias nos cursos de graduao que ajudam os
        alunos a aprender de forma independente, envolvendo estudos acadmicos,
        cientficos e culturais. Elas tm como objetivo ampliar o conhecimento e
        preparar o aluno para o mercado de trabalho.
      </S.Text>
      <S.Title>Princpios das Atividades Complementares:</S.Title>
      <S.Text>
        As ACs permitem que o aluno explore outras reas alm da sua formao
        principal, desenvolva pensamento crtico, participe de atividades
        sociais e cientficas, e se envolva com a comunidade.
      </S.Text>
      <S.Title>Como as Atividades Complementares so organizadas:</S.Title>
      <S.Text>
        As ACs devem comear logo no incio do curso e tm uma carga horria
        definida. O curso deve indicar que tipos de atividades podem ser feitas,
        quantas horas cada uma conta, e o coordenador do curso supervisiona se
        os alunos esto cumprindo as atividades corretamente.
      </S.Text>
      <S.Title>Tipos de Atividades Complementares:</S.Title>
      <S.Text>
        Existem diferentes tipos de ACs, como fazer matrias extras, participar
        de projetos de pesquisa, eventos acadmicos (seminrios, congressos), ou
        criar contedo cientfico (como artigos, livros, vdeos), alm de
        estgios e outras atividades que contribuam para a formao
        profissional.
      </S.Text>
      <S.Title>Validao das Atividades Complementares:</S.Title>
      <S.Text>
        Para que uma AC seja reconhecida, o aluno precisa apresentar um
        documento que comprove sua participao. O coordenador do curso avalia
        esse documento e decide se ele vale ou no como AC.
      </S.Text>
      <S.Title>Regras gerais para as Atividades Complementares:</S.Title>
      <p>1. Limite de carga horria:</p>
      <p>
        2. Um aluno pode participar de vrias atividades, como eventos ou
        estgios, mas apenas at um tero da carga horria total das ACs (216
        horas) pode vir de uma mesma atividade.
      </p>
      <p>
        3. Exemplo: Se o aluno fizer monitoria, no mximo 72 horas (1/3 de 216)
        podero ser validadas para isso.
      </p>
      <p>4. Validao de ACs de outras universidades:</p>
      <p>
        Se um aluno for transferido de outra universidade e realizou atividades
        complementares l, essas atividades podem ser aceitas, desde que feitas
        enquanto ele estava matriculado no curso anterior.
      </p>
      <S.Text>
        <b>Exemplo:</b> Se o aluno participou de um congresso enquanto estava na
        outra universidade, ele pode solicitar a validao desse evento.
      </S.Text>
      <S.Text>5. ACs durante o perodo de trancamento:</S.Text>
      <S.Text>
        Se o aluno trancar a matrcula, ele ainda pode realizar atividades
        complementares durante esse perodo.
      </S.Text>
      <S.Text>6. Atividades realizadas aps a entrada no curso:</S.Text>
      <S.Text>
        S so aceitas atividades feitas aps o incio do curso.
        <b>Exemplo:</b> Um curso de idiomas realizado antes de o aluno comear a
        faculdade no ser aceito como atividade complementar.
      </S.Text>
      <S.Text>7. Transferncia e ACs de outro curso:</S.Text>
      <S.Text>
        Se o aluno cursou disciplinas em outra faculdade ou outro curso, elas
        podem ser validadas como ACs.
        <b>Exemplo:</b> Se um aluno de Engenharia transferido para Administrao
        cursou "Introduo  Programao", essa disciplina pode ser validada
        como AC.
      </S.Text>
      <S.Text>8. Limite para disciplinas de outros cursos:</S.Text>
      <S.Text>
        Se um aluno fizer disciplinas em outro curso (mesmo na mesma faculdade),
        essas disciplinas podem contar como ACs, mas tambm seguem o limite de
        1/3 da carga horria.
        <b>Exemplo:</b> Se o aluno cursar "Direito Empresarial" enquanto faz
        Administrao, ele pode contar essa disciplina, mas apenas at 72 horas.
      </S.Text>
      <S.Text>
        Ao final do curso, o aluno dever ter integralizado 216 horas de
        Atividades Complementares, o equivalente a 12 crditos.
      </S.Text>
    </S.Content>
  );
};

export default Home;
